<template>
<div>
<header>
<div class="container">

    <router-link :to="{ name: 'main' }" class="flex-container">
      <div class="header__logo"></div>
      <div class="header__title">Каталог фильмов</div>
    </router-link>
  </div>
</header>

<main>
  <div class="container">

  <div v-if="movieLoading" class="lds-roller-container">
  <div class="lds-roller">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </div>

    <div v-if="movieLoadingFailed">
      Ошибка загрузки =(
    </div>

  <MovieView :movie="movie" />
  </div>
</main>
</div>
</template>

<script>
import MovieView from '@/components/MovieView.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: { MovieView },
  data() {
    return {
      movieData: null,
      movieLoading: false,
      movieLoadingFailed: false,
    };
  },
  computed: {
    movie() {
      return this.movieData
        ? this.movieData.search.filter(
          (i) => i.id === this.$route.params.id,
        )[0]
        : {};
    },
  },
  methods: {
    loadMovie() {
      this.movieLoading = true;
      this.movieLoadingFailed = false;
      axios.get(`${API_BASE_URL}/movies`).then(
        (response) => { this.movieData = response.data; },
      ).catch(
        () => { this.movieLoadingFailed = true; },
      ).then(
        () => { this.movieLoading = false; },
      );
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadMovie();
      },
      immediate: true,
    },
  },
};
</script>
