<template>
<div class="col-xs-12 col-sm-12 col-md-6 col-lg-7 find">
  <div class="find-icon"></div>
  <input type="text" placeholder="название фильма"
             v-model="currentTitle" />
</div>
</template>

<script>
export default {
  props: ['title'],
  computed: {
    currentTitle: {
      get() {
        return this.title;
      },
      set(value) {
        this.$emit('update:title', value);
      },
    },
  },
};
</script>
